<template>
    <div id="si-main">
        <dv-full-screen-container style="min-height:635 !import">
            <div class="main-container">
                <div class="main-container-left">
                    <dv-border-box-12 :color="['#0B1D71', '#7492DD']" class="main-container-left-top">
                        <div class="main-container-left-top-context">
                            <div id="speedchart" style="width: 100%;height:310px;"></div>
                        </div>
                    </dv-border-box-12>
                    <dv-border-box-8 class="main-container-left-timer" :color="['#0B1D71', '#7492DD']">
                        <div><small>总时长 </small>64 <small>S 当前</small> {{ second }} <small>S</small></div>
                    </dv-border-box-8>
                    <dv-border-box-12 :color="['#0B1D71', '#7492DD']" class="main-container-left-bottom">
                        <dv-decoration-11 class="main-container-left-bottom-title" :color="['#0B1D71', '#7492DD']">通道数据</dv-decoration-11>
                        <div class="main-container-left-bottom-context">
                            <CellGroup>
                                <Cell v-for="(item,index) in canlist" :title="item.title" :extra="item.extra" :key="index" />
                            </CellGroup>
                        </div>
                    </dv-border-box-12>
                </div>
                <div class="main-container-center">
                    <dv-border-box-11 class="main-container-center-top" title="场景数据回放">
                        <div id="container" class="container"></div>
                    </dv-border-box-11>
                    <dv-border-box-2 :color="['#0B1D71', '#7492DD']" class="main-container-center-bottom">
                        <video id="videoElement" controls :autoplay="false" width="100%" height="261">您的浏览器不支持HTML5播放，请更换浏览器！</video>
                    </dv-border-box-2>
                </div>
                <div class="main-container-right">
                    <dv-border-box-8 class="main-container-right-timer" :color="['#0B1D71', '#7492DD']">
                        <div>
                            <Icon type="ios-car" /> <small>自动驾驶模式</small></div>
                    </dv-border-box-8>
                    <dv-border-box-12 :color="['#0B1D71', '#7492DD']" class="main-container-right-bottom">
                        <div class="main-container-right-bottom-context">
                            <div class="diychart" v-for="(item,index) in diychart" :key="index" :id="'chart'+index">
                            </div>
                        </div>
                    </dv-border-box-12>
                </div>
            </div>
        </dv-full-screen-container>
        <!-- 设置弹框 -->
        <Modal v-model="settingstate" title="图表设置" @on-ok="okSetting()">
            <Alert closable>温馨提示：最少选择一条通道数据</Alert>
            <CheckboxGroup v-model="selectchart">
                <Checkbox :label="index" v-for="(item,index) in can.column" :key="index">{{item}}</Checkbox>
            </CheckboxGroup>
        </Modal>
    </div>
</template>

<script>
    import MapLoader from "@/plugins/amap.js";
    import flvjs from "flv.js/dist/flv";
    import Charts from '@jiaminghi/charts';
    import jsondata from '../../assets/json/can.json'
    var echarts = require('echarts');
    export default {
        data() {
            return {
                map: null, //地图图层MAP
                videourl: "https://fupoaiwo.github.io/video.flv", //视频URL
                flvPlayer: null, //flvPlayer
                videoElement: null,
                can: {
                    column: [], // can title 列
                    data: [] // can data 数据
                },
                coordinate: [], // 经纬度集合
                settimer: null, // 全局定时器
                totalsecond: 0, // 总读秒数
                second: -1, // 当前读秒数
                //MAP相关配置
                marker: null, // 车辆标记 Mark
                lineArr: null, // 线坐标集合
                polyline: null, // 历史轨迹线样式
                passedPolyline: null, // 历史轨迹路过线样式
                currentcd: [], //当前坐标点
                canlist: [], //can数据列表
                nowspeed: 0, //当前车速
                speedchart: null, //车速仪表盘DOM
                speedoption: {}, //车速仪表盘设置
                settingstate: false, //设置图表弹框状态
                selectchart: [], //   已选中chart集合
                diychart: [{
                        id: 4,
                        title: "GPS车速",
                        chart: null,
                        option: null,
                        data: []
                    },
                    {
                        id: 8,
                        title: "GPS海拔",
                        chart: null,
                        option: null,
                        data: []
                    },
                    {
                        id: 11,
                        title: '排气流量("kg/h")',
                        chart: null,
                        option: null,
                        data: []
                    }
                ], //自定义表单
            };
        },
        methods: {
            //项目初始化
            initialization() {
                //初始化全屏地图
                this.loadMap();
                //初始化视频播放器
                setTimeout(() => {
                    this.loadFlvPlear();
                    if (this.settimer) {
                        clearInterval(this.settimer)
                    } else {
                        this.settimer = setInterval(() => {
                            //this.second = Math.round(this.flvPlayer.currentTime);
                            this.second = Math.floor(this.flvPlayer.currentTime);
                            // console.log(this.second);
                        }, 500)
                    }
                    //加载历史轨迹
                    this.loadHtrack();
                    //初始化速度仪表盘
                    this.loadSpeed();
                    //加载图表集合
                    this.loadcharts();
                }, 2000);
                //初始化数据加载
                this.loadData();
            },
            //初始化车速仪表盘
            loadSpeed() {
                this.speedchart = echarts.init(document.getElementById('speedchart'));
                this.speedoption = {
                    // title: {
                    //     text: '当前车速',
                    //     // subtext: '纯属虚构',
                    //     left: 'center',
                    //     textStyle: { // 其余属性默认使用全局文本样式，详见TEXTSTYLE
                    //         color: '#fff',
                    //     }
                    // },
                    // backgroundColor: '#1b1b1b',
                    tooltip: {
                        formatter: "{a} <br/>{c} {b}"
                    },
                    toolbox: {
                        show: false,
                        feature: {
                            mark: {
                                show: true
                            },
                            restore: {
                                show: true
                            },
                            saveAsImage: {
                                show: true
                            }
                        }
                    },
                    series: [{
                        name: '速度',
                        type: 'gauge',
                        min: 0,
                        max: 220,
                        splitNumber: 11,
                        // radius: '50%',
                        axisLine: { // 坐标轴线
                            lineStyle: { // 属性lineStyle控制线条样式
                                color: [
                                    [0.09, 'lime'],
                                    [0.82, '#1e90ff'],
                                    [1, '#ff4500']
                                ],
                                width: 3,
                                shadowColor: '#fff', //默认透明
                                shadowBlur: 10
                            }
                        },
                        axisLabel: { // 坐标轴小标记
                            textStyle: { // 属性lineStyle控制线条样式
                                fontWeight: 'bolder',
                                color: '#fff',
                                shadowColor: '#fff', //默认透明
                                shadowBlur: 10
                            }
                        },
                        axisTick: { // 坐标轴小标记
                            length: 15, // 属性length控制线长
                            lineStyle: { // 属性lineStyle控制线条样式
                                color: 'auto',
                                shadowColor: '#fff', //默认透明
                                shadowBlur: 10
                            }
                        },
                        splitLine: { // 分隔线
                            length: 25, // 属性length控制线长
                            lineStyle: { // 属性lineStyle（详见lineStyle）控制线条样式
                                width: 3,
                                color: '#fff',
                                shadowColor: '#fff', //默认透明
                                shadowBlur: 10
                            }
                        },
                        pointer: { // 分隔线
                            shadowColor: '#fff', //默认透明
                            shadowBlur: 5
                        },
                        title: {
                            textStyle: { // 其余属性默认使用全局文本样式，详见TEXTSTYLE
                                fontWeight: 'bolder',
                                fontSize: 20,
                                fontStyle: 'italic',
                                color: '#fff',
                                shadowColor: '#fff', //默认透明
                                shadowBlur: 10
                            }
                        },
                        detail: {
                            backgroundColor: 'rgba(30,144,255,0.8)',
                            borderWidth: 1,
                            borderColor: '#fff',
                            shadowColor: '#fff', //默认透明
                            shadowBlur: 5,
                            offsetCenter: [0, '50%'], // x, y，单位px
                            textStyle: { // 其余属性默认使用全局文本样式，详见TEXTSTYLE
                                fontWeight: 'bolder',
                                color: '#fff'
                            }
                        },
                        data: [{
                            value: this.nowspeed,
                            name: 'km/h'
                        }]
                    }]
                };
                this.speedchart.setOption(this.speedoption);
            },
            //初始化视频播放器
            loadFlvPlear() {
                let that = this;
                that.videoElement = document.getElementById("videoElement");
                that.flvPlayer = flvjs.createPlayer({
                    type: "flv",
                    url: that.videourl
                });
                that.flvPlayer.attachMediaElement(that.videoElement);
                that.flvPlayer.load();
                that.videoElement.onplay = function() {
                    //视频播放事件
                    that.onVideoPlay();
                };
                that.videoElement.onpause = function() {
                    //视频暂停事件
                    that.onVideoPuse();
                };
                // that.flvPlayer.play();
            },
            //初始化全屏地图
            loadMap() {
                let that = this;
                MapLoader().then(AMap => {
                    that.map = new AMap.Map("container", {
                        center: [116.397428, 39.90923],
                        pitch: 20, // 地图俯仰角度，有效范围 0 度- 83 度
                        viewMode: '3D', // 地图模式
                        zoom: 17,
                        resizeEnable: true, //是否监控地图容器尺寸变化
                        mapStyle: "amap://styles/0d8c3dd312b132f0ad4d0391a26a45ad"
                    });
                });
            },
            //初始化 json 里的数据
            loadData() {
                this.can.column = jsondata.column;
                this.can.data = jsondata.data;
                this.coordinate = jsondata.coordinate;
            },
            //初始化历史轨迹
            loadHtrack() {
                let that = this;
                //设置地图中心点
                let onecoord = this.coordinate[0];
                this.map.setCenter(onecoord);
                //设置Mark
                this.marker = new AMap.Marker({
                    map: that.map,
                    position: onecoord,
                    icon: "/car.png",
                    offset: new AMap.Pixel(-18, -13),
                    autoRotation: true,
                    angle: -70,
                });
                //设置 lineArr 
                this.lineArr = this.coordinate;
                // console.log(JSON.stringify(this.lineArr));
                // 绘制轨迹
                this.polyline = new AMap.Polyline({
                    map: that.map,
                    path: that.lineArr,
                    showDir: true,
                    strokeColor: "#28F", //线颜色
                    // strokeOpacity: 1,     //线透明度
                    strokeWeight: 6, //线宽
                    // strokeStyle: "solid"  //线样式
                });
                // this.passedPolyline = new AMap.Polyline({
                //     map: that.map,
                //     // path: lineArr,
                //     strokeColor: "#AF5", //线颜色
                //     // strokeOpacity: 1,     //线透明度
                //     strokeWeight: 6, //线宽
                //     // strokeStyle: "solid"  //线样式
                // });
                // this.marker.on('moving', function(e) {
                //     that.passedPolyline.setPath(e.passedPath);
                // });
                this.map.setFitView();
            },
            //开始运行轨迹
            startAnimation() {
                this.marker.moveAlong(this.lineArr, 70);
            },
            //暂停历史轨迹
            pauseAnimation() {
                this.marker.pauseMove();
            },
            //继续动画
            resumeAnimation() {
                this.marker.resumeMove();
            },
            //停止动画
            stopAnimation() {
                this.marker.stopMove();
            },
            // 点击播放事件
            onVideoPlay() {
                // this.startAnimation();
            },
            // 点击暂停事件
            onVideoPuse() {
                // this.pauseAnimation();
            },
            //加载右侧图表集合
            loadcharts() {
                let that = this;
                for (let i = 0; i < this.diychart.length; i++) {
                    this.diychart[i].chart = echarts.init(document.getElementById('chart' + i));
                    this.diychart[i].option = {
                        axisLine: { // 坐标轴线
                            lineStyle: { // 属性lineStyle控制线条样式
                                color: [
                                    [0.09, 'lime'],
                                    [0.82, '#1e90ff'],
                                    [1, '#ff4500']
                                ],
                                width: 3,
                                shadowColor: '#fff', //默认透明
                                shadowBlur: 10
                            }
                        },
                        toolbox: {
                            show: true,
                            feature: {
                                myTool1: {
                                    show: true,
                                    title: '设置图表',
                                    icon: 'image:///setting.png',
                                    onclick: function() {
                                        that.settingcharts(i);
                                    }
                                },
                                myTool2: {
                                    show: true,
                                    title: '关闭窗口',
                                    icon: 'image:///close.png',
                                    onclick: function() {
                                        that.removecharts(i);
                                    }
                                }
                            }
                        },
                        grid: {
                            left: '2%',
                            right: '2%',
                            // bottom: '1%',
                            containLabel: true
                        },
                        tooltip: {
                            formatter: this.diychart[i].title + "<br/>{c}"
                        },
                        title: {
                            textStyle: { // 其余属性默认使用全局文本样式，详见TEXTSTYLE
                                fontWeight: 'bolder',
                                fontSize: 16,
                                fontStyle: 'italic',
                                color: '#fff',
                                shadowColor: '#fff', //默认透明
                                shadowBlur: 10
                            },
                            left: 'center',
                            text: this.diychart[i].title
                        },
                        xAxis: {
                            type: 'category',
                            // data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
                            axisLine: {
                                lineStyle: {
                                    type: 'solid',
                                    color: '#fff', //左边线的颜色
                                    width: '1' //坐标线的宽度
                                }
                            },
                            axisLabel: {
                                textStyle: {
                                    color: '#fff', //坐标值得具体的颜色
                                }
                            }
                        },
                        yAxis: {
                            type: 'value',
                            axisLine: {
                                lineStyle: {
                                    type: 'solid',
                                    color: '#fff', //左边线的颜色
                                    width: '1' //坐标线的宽度
                                }
                            },
                            axisLabel: {
                                textStyle: {
                                    color: '#fff', //坐标值得具体的颜色
                                }
                            }
                        },
                        series: [{
                            //data: [820, 932, 901, 934, 1290, 1330, 1320],
                            data: this.diychart[i].data,
                            type: 'line'
                        }]
                    };
                    this.diychart[i].chart.setOption(this.diychart[i].option);
                }
            },
            //删除右侧图表事件
            removecharts(index) {
                if (this.diychart.length == 1) {
                    this.$Notice.warning({
                        title: '警告',
                        desc: '最少保留一个窗口'
                    });
                    return;
                }
                this.diychart.splice(index, 1);
                this.loadcharts();
            },
            //设置右侧图表事件
            settingcharts(index) {
                let data = [];
                for (let i = 0; i < this.diychart.length; i++) {
                    data.push(this.diychart[i].id);
                }
                this.selectchart = data;
                this.settingstate = true;
            },
            //确定保存按钮
            okSetting() {
                if (this.selectchart.length == 0) {
                    this.$Notice.warning({
                        title: '警告',
                        desc: '最少保留一个窗口'
                    });
                    return;
                }
                let data = [];
                for (let i = 0; i < this.selectchart.length; i++) {
                    let id = this.selectchart[i];
                    let o = {
                        id: id,
                        title: this.can.column[id],
                        chart: null,
                        option: null,
                        data: []
                    }
                    data.push(o);
                }
                this.diychart = data;
                setTimeout(() => {
                    this.loadcharts();
                });
            }
        },
        mounted() {
            //初始化服务
            this.$nextTick(() => {
                this.initialization();
            })
        },
        watch: {
            second(newsecond, oldsecond) {
                //更新当前坐标位置
                this.currentcd = this.coordinate[newsecond];
                this.marker.setPosition(this.currentcd);
                //更新can数据
                let candata = [];
                for (let i = 0; i < this.can.column.length; i++) {
                    let can = {
                        "title": this.can.column[i],
                        "extra": this.can.data[newsecond][i] + ""
                    }
                    candata.push(can);
                }
                this.canlist = candata;
                //更新车速仪表盘速度
                this.nowspeed = this.can.data[newsecond][4];
                this.speedoption.series[0].data[0].value = this.nowspeed;
                this.speedchart.setOption(this.speedoption);
                //更新右侧图表数据
                for (let i = 0; i < this.diychart.length; i++) {
                    let id = this.diychart[i].id;
                    let data = [];
                    for (let s = 0; s < newsecond; s++) {
                        data.push(this.can.data[s][id]);
                    }
                    console.log(data)
                    this.diychart[i].option.series[0].data = data;
                    this.diychart[i].chart.setOption(this.diychart[i].option);
                }
            }
        }
    };
</script>

<style lang="less" scoped>
    @import "./index.less";
</style>